.categories {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.categorie__item {
  position: relative;
  box-sizing: border-box; }
  .categorie__item::after {
    content: "";
    border: 1px solid #adb7d0;
    box-sizing: border-box;
    position: absolute;
    opacity: 0.2;
    top: 50%;
    left: 50%;
    height: 100%;
    width: 100%;
    transform: translate(-50%, -50%);
    transition: 0.3s; }
  .categorie__item img {
    display: block;
    height: 100%;
    width: 100%;
    object-fit: contain; }
  .categorie__item_text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #adb7d0;
    font-size: 16px;
    line-height: 29px;
    text-transform: uppercase;
    text-align: center;
    transition: 0.3s; }
    .categorie__item_text span {
      font-weight: bold; }
  .categorie__item.active::after {
    border-color: #f1c86e;
    opacity: 1; }
  .categorie__item.active .categorie__item_text {
    color: #f1c86e; }
  .categorie__item:hover:not(.active)::after {
    border-color: #f1c86e;
    opacity: 1;
    opacity: 0.3;
    transition: 0.3s; }
  .categorie__item:hover:not(.active) .categorie__item_text {
    transition: 0.3s;
    color: #f1c86e;
    opacity: 0.6; }

